import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { WfhPolicyElement, WfhPolicyService } from 'modules/work-from-home/policy/wfh-policy.service';
import { STATUS } from 'config/constants/wfh-policy';

@Component({
  selector: 'app-list-wfh-policy',
  templateUrl: './list-wfh-policy.component.html',
  providers: [ WfhPolicyService ],
  encapsulation: ViewEncapsulation.None
})
export class ListWfhPolicyComponent implements OnInit {

  activeWfhPolicy: WfhPolicyElement;
  scheduledWfhPolicy: WfhPolicyElement;
  error: string;
  loading: boolean = false;

  constructor(
    private wfhPolicyService: WfhPolicyService
  ) {

    this.error = '';
  }

  ngOnInit() {

    const status = [
      STATUS.ACTIVE,
      STATUS.SCHEDULED
    ]
    const query = new Map<string, any>();
    query.set('wfh_policy_status', status.join(','));

    this.loading = true;

    this.wfhPolicyService.getPolicy().then(policies => {

      this.loading = false;
      for (const policy of policies) {
        if (policy.wfh_policy_status === STATUS.ACTIVE) {
          this.activeWfhPolicy = policy
        } else if (policy.wfh_policy_status === STATUS.SCHEDULED) {
          this.scheduledWfhPolicy = policy;
        }
      }

    }).catch(err => {

      this.error = err;
    });
  }
}
