import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ManageWfhPolicyComponent } from './manage-wfh-policy/manage-wfh-policy.component';
import { AuthenticationGuard } from 'core/services/authentication.service';
import { AuthorizationGuard } from 'core/services/authorization.service';
import { ListWfhPolicyComponent } from './list-wfh-policy/list-wfh-policy.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Policy'
    },
    children: [
      {
        path: '',
        redirectTo: 'list'
      },
      {
        path: 'list',
        data: {
          title: 'List',
          access: {
            module: 'work-from-home',
            entity: 'policy',
            action: 'find'
          }
        },
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        component: ListWfhPolicyComponent
      },
      {
        path: 'manage',
        data: {
          title: 'Manage',
          access: {
            module: 'work-from-home',
            entity: 'policy',
            action: 'manage'
          }
        },
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        component: ManageWfhPolicyComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WfhPolicyRoutingModule { }
