import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';
import { clone } from 'lodash';

import { EmployeeService } from 'modules/employee/employee.service';
import { WfhPolicy, WfhPolicyService } from 'modules/work-from-home/policy/wfh-policy.service';
import { Name } from 'modules/user/profile/profile.service';
import { PERIOD } from 'config/constants/wfh-policy';
import { BsDatepickerDirective } from 'ngx-bootstrap';
import { DatePipe } from '@angular/common';

class UserSearchItem {

  user_id: string;
  name: string;
  email_id: string;
}

const error_map = {
  date: 'Effective Start Date',
  count: 'Allowed Count',
  allowed_period: 'Allowed Period',
  loaned_count: 'Loaned Count',
  allowed_back_days: 'Allowed Back Days'
};

@Component({
  selector: 'app-manage-wfh-policy',
  templateUrl: './manage-wfh-policy.component.html',
  providers: [ WfhPolicyService, EmployeeService, DatePipe ],
  encapsulation: ViewEncapsulation.None
})
export class ManageWfhPolicyComponent implements OnInit {
  @ViewChild('errorModal') errorModal: ErrorModalComponent;
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('startDate') startDate: BsDatepickerDirective;

  wfhPolicy: WfhPolicy;
  error: string;
  invalid_attributes: Array<string>;

  from_users: boolean = false;
  user_list: UserSearchItem[] = [];
  loading_users: boolean = false;

  dateConfig: Object;

  wfh_period = clone(PERIOD);

  constructor(
    private wfhPolicyService: WfhPolicyService,
    private employeeService: EmployeeService,
    private datePipe: DatePipe
  ) {

    this.wfhPolicy = new WfhPolicy();
    this.error = '';

    this.invalid_attributes = [];

    const current_date = new Date();
    current_date.setDate(current_date.getDate() + 1);
    this.dateConfig = {
      minDate: current_date,
      dateInputFormat: 'YYYY-MM-DD',
      containerClass: 'theme-blue',
      showWeekNumbers: false
    };
  }

  ngOnInit() {

    this.__loadUsers();

    // this.wfhPolicyService.getWfhPolicy ()
    //   .then(wfhPolicy => this.wfhPolicy = wfhPolicy)
    //   .catch(err => {
    //     if (err.status === 404) {
    //       // not a problem if the current policy is not found.. may be this is the first time.
    //       this.wfhPolicy.maxAllowed = 0;
    //       this.wfhPolicy.allowedPeriod = ALLOCATION.WEEKLY;
    //       this.wfhPolicy.approval.required = true;
    //       this.wfhPolicy.startDate = new Date();
    //       // end year not needed.

    //       return;
    //     }

    //     this.error = err;
    //     this.errorModal.show();
    //   });
  }

  private __validate() {
    return true;
  }

  private __loadUsers() {

    this.loading_users = true;
    this.employeeService.getEmployees(null, 0, 0, ['_id', 'email_id', 'name'])
      .then(response => {

        this.user_list = response.users.map(user => {

          const item = new UserSearchItem();
          item.user_id = user._id;
          item.email_id = user.email_id;
          item.name = Name.getFullName(user.name);
          return item;
        });
        this.loading_users = false;

      }).catch(err => {

        this.error = err.error.message.text;
        this.errorModal.show();
      });
  }

  private __sanitizeWfhPolicy(wfhPolicy) {

    if (wfhPolicy.date !== undefined) {
      wfhPolicy.date = this.datePipe.transform(wfhPolicy.date, 'yyyy-MM-dd');
    }

    if (wfhPolicy.allowed_count === undefined) {
      wfhPolicy.allowed_count = null;
    }

    const approval = wfhPolicy.approval;
    if (approval && (approval.from_supervisor || (approval.users && approval.users.length > 0))) {
      wfhPolicy.approval.required = true;
    } else {
      wfhPolicy.approval = {
        required: false
      };
    }

    return wfhPolicy;
  }

  filterUsers(term: string, item: UserSearchItem) {

    const regex = new RegExp(term, 'i');
    return item.email_id.match(regex) || item.name.match(regex);
  }

  saveWfhPolicy() {

    if (this.__validate()) {

      let wfh_policy = JSON.parse(JSON.stringify(this.wfhPolicy));
      wfh_policy = this.__sanitizeWfhPolicy(wfh_policy);
      this.error = '';
      this.invalid_attributes = [];

      this.wfhPolicyService.putPolicy(wfh_policy).then(data => {

        this.successModal.show('/work-from-home/policy');

      }).catch(err => {

        this.error = err.error.message.text;
        if (err.error.invalid_attributes) {
          for (const key in err.error.invalid_attributes) {
            if (err.error.invalid_attributes.hasOwnProperty(key)) {
              this.invalid_attributes.push(error_map[key] ? error_map[key] : err.error.invalid_attributes[key]);
            }
          }
        }
        this.errorModal.show();
      });
    }
  }
}
