import { Injectable } from '@angular/core';
import { Api } from 'core/services/api.service';
import { PERIOD, STATUS } from 'config/constants/wfh-policy';

export class WfhApproval {

  required: boolean = false;
  from_supervisor: boolean = false;
  users: string[];
}

export class WfhPolicy {

  date: Date;
  allowed_count: number;
  allowed_period: PERIOD;
  allowed_back_days?: number;
  loaned_count?: number;
  approval: WfhApproval = new WfhApproval();
}

export class WfhPolicyElement extends WfhPolicy {

  _id: string;
  organization_id: string;
  start_date: Date;
  end_date: Date;
  wfh_policy_status: STATUS;
}

@Injectable()
export class WfhPolicyService extends Api {

  private _base_url: string = '/wfhs/policies';

  getPolicy(query ?: Map<string, any>): Promise<any> {

    return this.get(this._base_url);
  }

  putPolicy(policy: WfhPolicy): Promise<Object> {

    return this.put(this._base_url + '/manage', policy)
      .then(response => response);
  }
}
